<template>
    <v-card>
       bbb {{ resultArtistRecordParse }}
       <v-card-title>
        {{resultArtistRecordParse.artist_name}}
       </v-card-title>
    </v-card>
</template>
<script>
import {build_artist_url,parse_show_artist_dict} from '../../lib/lastfm'
import {getLastFMArtistCache,setLastFMArtistCache} from '../../lib/firestoreaccess'
import {sotre_const} from '../../lib/util'

export default {
    name:"resultArtistCard",
    data(){
        return{
            resultArtistRecordParse:Object
        }
    },
    props:{
        resultArtistRecord :{
            type:Object
        },
    },
    watch:{
        
    },
    mounted() {
         alert("hhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhh")
         if (this.resultArtistRecord){
            this.build_artist_detail()
         }
         
    },
    methods:{
        build_artist_detail(){
            //alert("hhhhhhhhhhhhh")
            const tmp_json = this.resultArtistRecord
            const artist_id = tmp_json.artist_id
            /**
             * DATABASE cache
             */
             const collectionName = sotre_const.collection.lastFmArtistInfoCache
             const firestoreResult = getLastFMArtistCache(collectionName,artist_id)
             if(firestoreResult != null){
                alert("aaaaaaaaaaaaaaaaaaaaaaaaaaa")
             }else{
                const aritst_name = tmp_json.artist_name_origine
                const lastfm_url = build_artist_url(aritst_name)
                alert(lastfm_url)

                fetch(lastfm_url,{
                /**
                 * condition
                 */
                }).then(res => res.json()
                ).then(data =>{
                    //alert(JSON.stringify(data))
                    const artist_lastfm =data.artist
                    const artist_lastfm_parse = parse_show_artist_dict(artist_lastfm)
                    const artist_id = artist_lastfm_parse.artist_id
                    Object.assign(artist_lastfm_parse, tmp_json);
                    this.resultArtistRecordParse = artist_lastfm_parse
                    setLastFMArtistCache(
                        sotre_const.collection.lastFmArtistInfoCache
                        ,artist_id
                        ,artist_lastfm_parse
                    )
                }).catch(
                    err =>{
                        alert("B"+err)
                    }
                )

             }

             /** */
           
            

            
        }
    }
}
</script>
