//https://ws.audioscrobbler.com/2.0/?method=artist.getinfo&artist=Cher&api_key=YOUR_API_KEY&format=json
const URL_BASE ="https://ws.audioscrobbler.com/2.0/";
const LAST_FM_APIKEY = "e5e11d4e4c6b55cbee2a8257c4fa3bac"; //get tagg
const PATH_NAME = "/2.0/";
const IMAGE_SIZE_EXTRALARGE = "extralarge";

const METHODS = {
    "info":"artist.getinfo"
}

const RES_FORMAT ={
    "json":"json",
    "xml":"xml"
}

const build_artist_url = (artist_str) => {
    let url = new URL(URL_BASE);
    url.pathname = PATH_NAME;
    const method = METHODS["info"]
    const json = RES_FORMAT["json"]
    const params = {
        "method":method,
        "artist":artist_str,
        "api_key":LAST_FM_APIKEY,
        "format":json
    }
    const search_param_obj = new URLSearchParams(params);
    url.search = search_param_obj.toString()
    return url.href;
}

const parse_show_artist_dict = (artist_dict) =>{
    //alert(JSON.stringify(artist_dict));
    const res_dict={};
    const artist_name = artist_dict.name;
    res_dict["artist_name"] = artist_name;
    const mbid = artist_dict.mbid;
    res_dict["mbid"] = mbid;
    const image_array = Array.from(artist_dict.image)  ;
    //alert(JSON.stringify(image_array))
    for(let element in image_array){
        const tmp_rec_dict = image_array[element];
        //alert(JSON.stringify(tmp_rec_dict))
        const pic_size = tmp_rec_dict.size;
        //alert(pic_size)
        if(pic_size == IMAGE_SIZE_EXTRALARGE){
            //alert(JSON.stringify(tmp_rec_dict))
            res_dict["image_url"] = tmp_rec_dict["#text"];
            //alert(JSON.stringify(res_dict))
            break;
        }
    }
    const tags = artist_dict.tags;
    const tagss =tags.tag;
    const tagss_array = Array.from(tagss);
    const res_tag_array =[]
    for(let indexx in tagss_array){
        const tmp_tag_rec = tagss_array[indexx];
        const tag_str = tmp_tag_rec.name;
        res_tag_array.push(tag_str);
    }
    res_dict["tags"] = res_tag_array;
    const bio = artist_dict.bio;
    const links = bio.links;
    const link = links.link;
    const href = link.href;
    res_dict["artist_info_link"] =href;
    const content = bio.content;
    //alert(content)
    res_dict["artist_content_en"] = content;
    return res_dict
}
export{build_artist_url,parse_show_artist_dict}