<template>
  <v-app>
    <v-card
      color="red lighten-2"
      dark
    >
      <v-card-title class="text-h5 red lighten-3">
        Recomendation Sample
      </v-card-title>
      <v-card-text></v-card-text>
      <v-card-text>
        <v-autocomplete
          v-model="item_record_data_value"
          :items="items"
          :loading="isLoading"
          :search-input.sync="search"
          color="white"
          item-text="artist_name_trance_remove_kakko"
          item-value="artist_name_trance_remove_kakko"
          label="Public APIs"
          placeholder="Start typing to Search"
          prepend-icon="mdi-database-search"
          return-object
          @change="(event) => searchNnResult(event)"
        >
        </v-autocomplete>
      </v-card-text>
      
      <PROFILE 
          :artist_profile_result_vue="artist_profile_result"
          :is_list_transision="is_list_transision"
      ></PROFILE>
    </v-card>
    <DIALOG
    ></DIALOG>
    <!--
    <v-main>
      <router-view/>
    </v-main>-->
    <RSLUT></RSLUT>
  </v-app>
</template>

<script>
import RSLUT  from './components/resultbody.vue'
import PROFILE from'./components/recomendFromBar.vue'
import DIALOG from './components/BaseEffectParts/basiccirculerdialog.vue'
export default {
  name: 'App',

  components: {
    RSLUT,PROFILE,DIALOG
  },

  data () {
    return {
      descriptionLimit: 60,
      entries: [],
      isLoading: false,
      res_isloading: false,
      model: null,
      search: null,
      artist_profile_result:null,
      is_list_transision:false,
      artist_id_prefix:"discogs_artist_"
    }
  },
  computed: {
    items () {
      return this.entries.map(entry =>
        {
          return entry
        }
      )
    },
    search_result_vuex(){
      return this.$store.getters.search_result
    }
  },
  watch: {
    async search (val) {
      if (this.isLoading) return

      const w_size = val.length
      if(w_size <1) return
      this.isLoading = true

      const param_d = {'suggest': val}
      const body = JSON.stringify(param_d)
      alert(body)
      // Lazily load input items
      //https://dubdubjazzsamplerecomendbackend.com/suggest
      fetch('http://localhost:3333/suggest',{
        method: 'POST',
        headers: {
          accept: 'application/json',
          'Content-Type': 'application/json' 
        },
        body:body
      })
        .then(res => res.json())
        .then(res =>{
          const tmp_res_li = res["result"];
          tmp_res_li.forEach(element => {
            const record = element["_source"]
            const artsit_id = record["artist_id"]
            record["discogs_artist_id"] = this.artist_id_prefix +artsit_id
            this.entries.push(record)
          });

        }).catch(err =>{
          alert(err)
        }).finally(() => (this.isLoading = false))
    },
  },
  methods:{
    async searchNnResult(a){
      const target_w = a["artist_name_trance_remove_kakko"]
      //alert("bbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbbb     "+JSON.stringify(a))
      const target_w_size = target_w.length
      if(target_w_size <1)return

      

      this.res_isloading = true
      //dispatch
      this.$store.dispatch('setRes_dialog_flgAction', this.res_isloading)
      //
      const param_d = {'nn_neighbor_artist': a}
      const body = JSON.stringify(param_d)
      //alert(body)
      //https://dubdubjazzsamplerecomendbackend.com/nnResult
      fetch('http://localhost:3333/nn_neighbor_artist',{
        //method: 'post',
        method:'POST',
        headers: {
          accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body:body
      }).then(
        res =>res.json()
      ).then(
        res=>{
          const update_relut = res["nn_neighbor_artist_result"];
          alert("NNRESULT ==>> "+update_relut)
          this.$store.dispatch('setSearchResultAction', update_relut)          
        }
      ).catch(
          err =>{
            alert("B"+err)
            }
      ).finally(
        () => (
          //dispatch
          this.res_isloading = false,
          this.$store.dispatch('setRes_dialog_flgAction', this.res_isloading)
          //
        )
      );
      
    },
  }
};
</script>
